<template>
  <div id="orderdatails">
    <div style="width: 100%; height: 50px; border-bottom: 0.1px solid #e7e7e7">
      <el-page-header
        style="padding: 15px"
        @back="goBack"
        content=""
      >
      </el-page-header>
    </div>
    <h3 style="text-align: center"></h3>
    <el-table
      ref="multipleTable"
      :data="detaillist"
      border
      tooltip-effect="dark"
      :header-cell-style="{ background: '#f7f7f7' }"
      style="width: 802px; margin: 10px auto"
    >
      <el-table-column prop="goodsId" label="" width="200">
      </el-table-column>
      <el-table-column label="" width="200">
        <template slot-scope="scope">{{ scope.row.goodsName }}</template>
      </el-table-column>
      <el-table-column prop="goodsPrice" label="" width="200">
      </el-table-column>
      <el-table-column prop="orderNum" label="" width="200">
      </el-table-column>
    </el-table>
    <el-dialog
      title=""
      :visible.sync="ordervisible"
      :modal-append-to-body="false"
    >
      <el-form ref="form" label-width="120px" style="margin: 20px 20px">
        <el-form-item label="">
          <el-input placeholder=""></el-input
        ></el-form-item>
        <el-form-item label=""
          ><el-input placeholder=""></el-input
        ></el-form-item>
        <el-form-item label=""
          ><el-input placeholder=""></el-input
        ></el-form-item>
        <el-form-item label=""
          ><el-input placeholder=""></el-input
        ></el-form-item>
        <el-form-item label=""
          ><el-switch v-model="delivery"></el-switch
        ></el-form-item>
        <el-form-item label=""
          ><el-input placeholder=""></el-input
        ></el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="ordervisible = false"> </el-button>
        <el-button type="primary" @click="submit()"> </el-button>
      </div>
    </el-dialog>
    <div style="width: 300px; margin: 50px auto">
      <el-button type="primary" @click="ordervisible = true"
        ></el-button
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "orderdetails",
  data() {
    return {
      detaillist: [],
      delivery: true,
      ordervisible: false,
    };
  },
  created() {
    this.$axios
      .post("/order/detail", { id: this.$route.params.id })
      .then((res) => {
        this.detaillist = res.data;
      })
      .catch((err) => {
        window.console.log(err);
      });
  },
  methods: {
    goBack() {
      this.$router.replace("/order");
    },
    submit() {
      this.$data.tableData2 = [];
      alert("");
      this.$router.replace("/order");
    },
  },
};
</script>

<style scoped>
.el-input {
  width: 400px;
}
</style>